body {
  font-family: system-ui;
  overflow: hidden;
  background: rgb(64, 64, 64)
}

.App{
  position: absolute;
  top: var(--border-size);
  left: var(--border-size);
  right:var(--border-size);
  bottom: var(--border-size);
  background: var(--app-bg);
}

::-webkit-scrollbar {
  width: 8px;
  height: 10px;
}

::-webkit-scrollbar-thumb {
  border: 2px solid var(--bg);
  border-radius: 10px;
  background: rgba(73, 73, 73, 0.7);
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

button {
  border: none;
  cursor: pointer;
  -webkit-app-region: no-drag;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--text-sub-color);
}

:root {
  --border-size: 1px;
  --app-bg: rgb(38, 38, 38);
  --title-bar-color: rgb(32, 32, 32);
  --title-bar-height: 32px;
  --text-color: rgb(255, 255, 255);
  --text-sub-color: rgb(232, 232, 232);
  --sub-color: rgb(49, 49, 49);
  --line-color: rgb(71, 71, 71);
  --btn-bg: rgb(97, 97, 97);
  --primary: rgb(11, 108, 233);
  --item-bg: rgb(34, 34, 34);
  --input-bg: rgb(35, 35, 35);
  --input-placeholder: rgb(180, 180, 180);
  --input-border: 1px solid rgb(78, 78, 78);
  --reverse-color: white;
  --reverse-color1: black;
  --custom-radio-bg: rgba(97, 96, 96, 0.3);
  --custom-radio-bg-active: rgb(10, 104, 159);
}

[class="light-theme"] {
  --app-bg: rgb(255, 255, 255);
  --title-bar-color: rgb(161, 161, 161);
  --title-bar-height: 32px;
  --text-color: rgb(0, 0, 0);
  --text-sub-color: rgb(45, 45, 45);
  --sub-color: rgb(239, 239, 239);
  --line-color: rgb(212, 212, 212);
  --btn-bg: rgb(214, 215, 223);
  --primary: rgb(11, 108, 233);
  --item-bg: rgb(226, 226, 226);
  --input-bg: rgb(213, 213, 213);
  --input-placeholder: rgb(35, 35, 35);
  --input-border: 1px solid rgb(78, 78, 78);
  --reverse-color: black;
  --reverse-color1: white;
  --custom-radio-bg: rgba(97, 96, 96, 0.3);
  --custom-radio-bg-active: rgb(10, 104, 159);
}

.app-main-content {
  overflow-y: auto;
  position: absolute;
  top: 32px;
  max-width: 750px;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  height: calc(100% - 32px);
}

.app-main-content.no-electron {
  top: 0 !important;
}

.content-login {
  width: 300px;
  margin: 15px auto;
  padding: 15px;
  border-radius: 10px;
  background: var(--sub-color);
}

.sub-content {
  margin: 15px;
  padding: 15px;
  border-radius: 10px;
  background: var(--sub-color);
}

.sub-content.header {
  top: 0;
  position: sticky;
}

.one-line {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.line {
  width: 100%;
  height: 2px;
  margin: 10px 0;
  background: var(--line-color);
}

.no-margin {
  margin: 0px !important;
}

.no-margin-top {
  margin-top: 0px !important;
}

.text-title {
  text-align: center;
  font-weight: bold;
  margin-top: 5px;
  margin-bottom: 20px;
  color: var(--text-color);
}

.text-sub-title {
  margin-top: 5px;
  margin-bottom: 20px;
  color: var(--text-color);
}

.text-sub-normal {
  margin-top: 5px;
  margin-bottom: 5px;
  color: var(--text-color);
}

input {
  width: calc(100% - 22px);
  padding: 10px;
  border-radius: 8px;
  border: none;
  font-size: 14px;
  margin-bottom: 10px;
  color: var(--text-color);
  background: var(--input-bg);
}

::placeholder {
  color: var(--input-placeholder);
}

.custom-dropdown {
  user-select: none;
  width: calc(100% - 18px);
  padding: 8px;
  border-radius: 8px;
  font-size: 14px;
  border: none;
  overflow: hidden;
  margin-bottom: 10px;
  color: var(--text-color);
  background: var(--input-bg);
}

.custom-dropdown-none {
  color: var(--input-placeholder);
}

.custom-dropdown-list {
  display: none;
  margin-top: 10px;
  border-radius: 5px;
  background: var(--item-bg);
}

.custom-dropdown-list.is-active {
  display: block;
}

.center {
  display: flex;
  justify-content: center;
}

.button {
  padding: 7px 12px;
  margin: 5px;
  border-radius: 8px;
  color: var(--text-color);
  background: var(--btn-bg);
}

.button.primary {
  color: white;
  background: var(--primary);
}

.button.out {
  color: white;
  font-weight: bold;
  background: rgb(162, 37, 37);
}

.button.special {
  width: calc(100% - 10px);
}

.draggable {
  -webkit-app-region: drag;
}

.no-draggable {
  -webkit-app-region: no-drag;
}

.title-bar {
  z-index: 999999999;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: var(--title-bar-color);
}

.title-hide {
  display: none;
}

.title-bar-name {
  display: inline;
  position: relative;
  font-size: 12px;
  color: white;
  font-weight: 500;
  letter-spacing: 1px;
  border-radius: 4px;
  padding: 5px 8px;
  top: -2px;
  left: 3px;
  text-shadow: 1px 0px 5px rgb(0, 0, 0);
  background: rgb(123, 122, 185);
}

.title-bar-right {
  float: right;
}

#min-btn:hover,
#max-btn:hover {
  background: rgb(122, 122, 122);
}

#close-btn:hover {
  background: rgb(209, 0, 0);
}

.title-bar-button {
  float: left;
  position: relative;
  width: 46px;
  height: var(--title-bar-height);
  background: transparent;
}

.dropdown-toggle {
  width: 100% !important;
}

.dropdown-menu.show {
  width: calc(100% - 24px) !important;
}

.center {
  display: block;
  text-align: center;
}

.profile-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid var(--sub-color);
  padding: 10px;
  background: var(--title-bar-color);
}

.profile-title {
  color: var(--text-color);
  font-weight: bold;
}

.profile-title-sub {
  font-size: 14px;
  color: var(--text-sub-color);
}

.test-content {
  padding: 15px;
}

.custom-radio {
  border-radius: 2px;
  padding: 5px 7px;
  margin: 5px;
  color: var(--reverse-color);
  background: var(--custom-radio-bg);
}

.custom-radio.is-active {
  color: rgb(241, 241, 241);
  background: var(--custom-radio-bg-active);
}

.result-item {
  padding: 15px;
  margin: 10px 0;
  border-radius: 8px;
  color: var(--text-sub-color);
  background: var(--item-bg);
}

.preview-item {
  position: relative;
  padding: 15px;
  margin-top: 35px;
  border-radius: 8px;
  background: var(--item-bg);
}

.preview-item-id {
  position: absolute;
  top: -15px;
  left: -10px;
  font-size: 14px;
  font-weight: bold;
  padding: 5px;
  border-radius: 8px;
  color: var(--reverse-color1);
  background: var(--reverse-color);
}

.preview-item-delete {
  position: absolute;
  top: -15px;
  right: -10px;
  font-size: 14px;
  font-weight: bold;
  padding: 5px 10px;
  border-radius: 8px;
  color: rgb(231, 231, 231);
  background: rgb(228, 17, 17);
}

.margin-left {
  margin-left: 5px;
}
